@import '_vars';
@import '_fonts';

// =============================================================================
// Global
// =============================================================================

body {
	padding: 0;
	margin: 0;
	background: $background-color no-repeat;
	font-family: $main-font;
	font-size: $text-size;
	font-weight: 400;
	color: $secondary-color;
}

.wrapper {
	width: 100%;
	max-width: $main-wrapper;
	margin: 0 auto;
}

.flexed-between {
	display: flex;
	justify-content: space-between;
	align-items: center;
}

.section {
	padding-top: 146px;
}

.logo {
	font-weight: 700;
	font-size: 20px;
	text-transform: uppercase;
	color: $secondary-color;
	text-decoration: none;
	letter-spacing: 1.6px;
}

.btn {
	font-weight: 600;
	font-size: 14px;
	color: $primary-color;
	text-decoration: none;
}

.btn_outline {
	padding: 10px 20px;
	border: 1px solid $primary-color;
	border-radius: 41px;
	transition: all 0.2s ease-out;

	&:hover {
		background: $primary-color;
		color: $secondary-color;
	}
}


.footer {
	height: 100vh;
	display: flex;
	justify-content: center;
	align-items: center;
	background: #000;
}


// =============================================================================
// Header
// =============================================================================

.header {
	width: 100%;
	position: fixed;
	top: 40px;
	left: 0;
	z-index: 9;
}

.header__menu {
	display: flex;
	align-items: center;
}

.header__menu-list {
	padding: 0;
	margin: 0 47px 0 0;
	display: flex;
	list-style: none;
}

.header__menu-item {
	margin-right: 54px;

	&:last-of-type {
		margin-right: 0;
	}
}

.header__menu-link {
	font-weight: 500;
	font-size: 14px;
	color: $primary-color;
	text-decoration: none;
	transition: all 0.2s ease-out;

	&:hover {
		color: $secondary-color;
	}
}

// =============================================================================
// Slider main
// =============================================================================

.splide .splide__track--draggable {
	user-select: text;
}

.splide__pagination {
	position: absolute;
  top: 50%;
  right: 60px;
  flex-direction: column;
  transform: translateY(-50%);
}

.splide__pagination__page {
	display: flex;
	justify-content: center;
	align-items: center;
	width: 16px;
	height: 16px;
	background: none;
	margin-bottom: 28px;
	padding: 0;
	border: none;
	cursor: pointer;

	&:after {
		content: " ";
		width: 4px;
		height: 4px;
		display: block;
		background: #F8D3EB;
		border-radius: 10px;
		transition: all 0.2s ease-out;
	}

	&.is-active:after {
		height: 20px;
		cursor: default;
	}
}

.splide__pagination li:last-of-type .splide__pagination__page {
	margin-bottom: 0;
}

// =============================================================================
// Hero section
// =============================================================================

.hero {
	display: flex;
}

.hero__image {
	margin-right: 157px;
}

.hero__content {
	display: flex;
	flex-direction: column;
	align-items: flex-start;
}

.hero__welcome {
	color: $primary-color;
	font-size: 20px;
	font-weight: 700;
	letter-spacing: 0.6px;
	text-transform: uppercase;
	margin-bottom: 14px;
}

.hero__title {
	color: $secondary-color;
	font-size: 48px;
	font-weight: 900;
	line-height: 109%; /* 52.32px */
	margin-bottom: 25px;
}

.hero__text {
	color: $secondary-color;
	font-size: 16px;
	font-weight: 400;
	line-height: 180%; /* 28.8px */
	margin-bottom: 40px;
}

.hero__btn {
	display: flex;
	position: relative;
	// background: $primary-color;
	border-top: 1px solid $primary-color;
	border-bottom: 1px solid $primary-color;
	transform: skew(0, -6deg);

	&:before {
		content: " ";
		display: block;
		// background: $primary-color;
		width: 30px;
	  height: 100%;
		border-top-left-radius: 8px;
	  border-bottom-left-radius: 20px;
		border-top: 1px solid $primary-color;
		border-bottom: 1px solid $primary-color;
		border-left: 1px solid $primary-color;
	  transform: skewX(-12deg);
	  position: absolute;
	  top: -1px;
	  left: -14px;
	}

	&:after {
		content: " ";
		display: block;
	  width: 30px;
	  height: 100%;
		// background: $primary-color;
	  border-top-right-radius: 8px;
	  border-bottom-right-radius: 18px;
		border-top: 1px solid $primary-color;
		border-right: 1px solid $primary-color;
		border-bottom: 1px solid $primary-color;
	  transform: skewX(18deg);
	  position: absolute;
	  top: -1px;
	  right: -18px;
	}

	.btn__text {
		display: block;
		color: $primary-color;
		padding: 20px 26px;
		transform: skew(0, 6deg);
		font-size: 15px;
		font-weight: 500;
		transition: color 0.2s ease-out;
	}
}

.hero__btn:hover {
	background: $primary-color;
	transform: skew(0, -6deg);

	&:before {
		background: $primary-color;
	  top: -1px;
	  left: -14px;
	}

	&:after {
		background: $primary-color;
	  top: -1px;
	  right: -18px;
	}

	.btn__text {
		color: $secondary-color;
	}
}
